.header {
    padding: 30px 0;
    position: absolute;
    z-index: 999;
    width: 100%;
    top: 0;
    left: 0;
    transition: 0.3s ease;
    &.fixed-top {
        padding: 10px 0;
        position: fixed;
        background-color: rgba($body, 0.9);
        box-shadow: 0 0px 8px rgba(0, 0, 0, 0.07);
    }
    .navbar-nav {
        .nav-item {
            padding: 0 30px;
            position: relative;
            transition: all ease 0.3s;
            @media (max-width: $xl) {
                margin-bottom: 15px;
            }
            &::before {
                content: " ";
                position: absolute;
                height: 17px;
                width: 2px;
                background-color: $white;
                top: calc(50% - 8.5px);
                right: 0;
                @media (max-width: $xl) {
                    display: none;
                }
            }
            &:last-child {
                margin-right: 0;
                &::before {
                    display: none;
                }
            }
            .nav-link {
                color: $white;
                @extend %font-18;
                display: flex;
                transition: all ease 0.3s;
                align-items: center;
            }
            &:hover,
            &:focus {
                .nav-link {
                    color: #77edf8;
                    background-color: transparent;
                }
                .dropdown-toggle::after {
                    transition: all ease 0.3s;
                    background-image: url(../img/Icons/down.svg) !important;
                }
            }

            .dropdown-menu {
                background-color: rgba(220, 204, 255, 0.6);
                padding-top: 46px;
                margin-top: -46px;
                border-radius: 4px;
                padding-left: 4px;
                padding-right: 4px;
                padding-bottom: 20px;
                width: 100%;
                transition: all ease 0.3s;
                @media (max-width: $xl) {
                    width: fit-content;
                    padding-top: 56px;
                    padding-left: 15px;
                    padding-right: 15px;
                    padding-bottom: 15px;
                    margin-left: -15px;
                }
                li {
                    text-align: center;
                    .dropdown-item {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        color: $white;
                        @extend %font-18;
                        &:hover,
                        &:focus {
                            color: #77edf8;
                            background-color: transparent;
                        }
                    }
                }
            }
        }
    }
}

/* ============ desktop view ============ */
@media all and (min-width: 992px) {
    .dropdown-menu {
        left: 0;
    }
    .dropdown-menu li {
        position: relative;
    }
    .dropdown-menu .submenu {
        padding-top: 20px !important;
        display: none;
        position: absolute;
        left: calc(100% + 1px);
        background-color: rgba(220, 204, 255, 0.8) !important;
        top: -7px;
        .submenu {
            background-color: rgba(220, 204, 255, 1) !important;
        }
    }
    .dropdown-menu .submenu-left {
        right: 100%;
        left: auto;
    }

    .dropdown-menu > li:hover > .submenu {
        display: block;
    }
}
/* ============ desktop view .end// ============ */

/* ============ small devices ============ */
@media (max-width: 991px) {
    .dropdown-menu .dropdown-menu {
        margin-left: 0.7rem;
        margin-right: 0.7rem;
        margin-bottom: 0.5rem;
    }
}

.dropdown-toggle::after {
    display: inline-block;
    margin-left: 0;
    vertical-align: 0;
    content: "";
    background-image: url(../img/Icons/down-white.svg);
    border: none !important;
    background-size: 10.01px 6.01px;
    background-position: center;
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;
}

.dropdown-submenu::after {
    display: inline-block;
    margin-left: 0;
    vertical-align: 0;
    content: "";
    background-image: url(../img/Icons/down-white.svg);
    border: none !important;
    background-size: 10.01px 6.01px;
    background-position: center;
    background-repeat: no-repeat;
    width: 24px;
    height: 6.01px;
    transform: rotate(-90deg);
}

.navbar-brand {
    max-width: 60%;
}

.dropdown-menu {
    .nav-item {
        &::before {
            display: none !important;
            content: "" !important;
        }
    }
}

@media (max-width: 1199px) {
    #main_nav {
        margin-top: 15px;
        transition: 0s;
        background-color: $primary;
        padding: 20px 15px;
        border-radius: 15px;
    }
}

.navbar-toggler[aria-expanded="true"] {
    .navbar-toggler-icon {
        background-image: url(../img/close-white.svg);
        background-repeat: no-repeat;
        background-size: 16px;
        background-position: center;
    }
}

.nav-up {
    top: -120px;
}

.whatsapp-flutuante {
    background-image: url(../img/whatsapp.png);
    background-size: cover;
    background-position: center center;
    position: fixed;
    bottom: 40px;
    right: 40px;
    z-index: 5;
    height: 50px;
    width: 50px;
    text-align: center;
    transition: opacity 0.5s;
    filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.4));
    a {
        display: inline-block;
        width: 100%;
        height: 100%;
    }
    i {
        color: $white;
        font-size: 21px;
        line-height: 21px;
    }
    &:hover {
        opacity: 0.6;
    }
    @media screen and (max-width: $sm) {
        bottom: 20px;
        right: 20px;
    }
}
